<template>
    <div class="info3" style="height: 30%">
        <div class="ksh_left1_title" style="margin-bottom: -3%;">
            <p>设备类型收入占比</p>
        </div>
        <div id="echarts_5" class="charts5" style="height: 100%;" ></div>
    </div>
</template>
<script>
    export default {
        name:'Devicetype',
         mounted(){
            this.echarts_5();
        },
        data(){
            return{
                machine_list:[], //机器列表
                machine_count:[], //机器数
                proportion:[]   //百分比
            }
        },
        methods:{
            async echarts_5(){

                let res= await this.$axios.get(this.$store.state.reqUrl+'/pc/index/type_income');
                let newData=res.data;
                newData.forEach((e,i,all)=>{
                    this.machine_list.push(e.name);
                    this.machine_count.push(e.value);
                    this.proportion.push(e.proportion);
                })
                var newMachine_list=this.machine_list;
                var newMachine_count=this.machine_count;
                var newProportion=this.proportion;
                var myChart = this.$echarts.init(document.getElementById('echarts_5'));
                var option = {
                    tooltip : {
                        trigger: 'item',
                        formatter: "{a} <br/>{b} : {c} ({d}%)"
                    },
                    color:['#73feff','#f1ff61','#00c5c0','#86b838'],
                    legend: {
                        orient: 'vertical',
                        data: newMachine_list,
                        textStyle: {
                            color: '#fff'
                        },
                        itemGap: 20,
                        formatter:function (name) {
                            var index = 0;
                            var clientlabels =newMachine_list;
                            var clientcounts = newMachine_count;
                            clientlabels.forEach(function(value,i){
                                if(value == name){
                                    index = i;
                                }
                            });
                            return name + "  " +newProportion[index];

                        },
                        x : '6%',
                        y:'24%'
                    },
                    series : [
                        {
                            name:'收入占比',
                            type: 'pie',
                            radius : '60%',
                            center: ['50%', '50%'],
                            label: {
                                normal: {
                                    show: false,
                                    position: 'center'
                                },
                                emphasis: {
                                    show: false

                                }
                            },
                            data:newData
                        }
                    ]
                };

                myChart.setOption(option);
            }
        }
    }
</script>