<template>
   <div id="map" class="map" style="height:100%;position: relative; z-index: 10; opacity: 0.9;"></div>
</template>
<script>
    import 'echarts/map/js/china.js';
    export default {
        name:'Map',
        data(){
            return{
                option:{},
                newSeries:{},
                newData:{},
                n:0,
                newColor:{},
                newOff:true,
                result:[],
                newRes:[]
            }
        },
        mounted(){
            setInterval(this.getMap,1000);
        },
        watch:{
             option:function(){
                 var myChart = this.$echarts.init(document.getElementById('map'));
                 this.option.series[0].data = this.newData;
                 this.n=0;
                 this.option.series[0].itemStyle.normal.color = this.newColor;
             }
        },
        methods:{
         async getMap(){
                let myChart = this.$echarts.init(document.getElementById('map'));
                 let mapData=[];
                // let result=await this.$axios.get('/api/sever/test/cs');
                 let result=await this.$axios.get(this.$store.state.reqUrl+'/sever/test/cs');
                 let data=result.data;
                 if(data!=null){
                     for(var i=0;i<data.length;i++){
                         mapData.push({
                             name:data[i].name,
                             value:data[i].value
                         })
                     }
                 }

                  var n = 0;
                  var convertColor = function () {
                      var colorArr = ["#2cb06d", "#6da3f4", "#4ec5c1", "#ceca5f"];
                      var color = colorArr[n++];
                      return color
                  };
                  this.newColor=convertColor();
                  let mapNewResult=mapData;
                  var option = {
                      geo: {
                          map: 'china',
                          label: {
                              normal: {
                                  show: true,//显示省份标签
                                  textStyle:{color:"#fff"}//省份标签字体颜色
                              },
                              emphasis: {
                                  show: false
                              }
                          },
                          silent: true,
                          roam: true,
                          itemStyle: {
                              normal: {
                                  borderWidth: .5,//区域边框宽度
                                  borderColor: '#fff',//区域边框颜色
                                  areaColor:"#162532",//区域颜色
                              },
                              emphasis: {
                                  borderWidth: .5,
                                  borderColor: '#fff',
                                  areaColor:"#162532",
                              }
                          }
                      },
                      layoutCenter: ['50%', '60%'],
                      // 如果宽高比大于 1 则宽度为 100，如果小于 1 则高度为 100，保证了不超过 100x100 的区域
                      layoutSize: 1000,
                      series: [
                          {
                              type: 'effectScatter',
                              coordinateSystem: 'geo',
                              animation: true,
                              // mapType: 'china',
                              data:mapNewResult,
                              symbolSize: function (val) {
                                  return 15;
                              },
                              showEffectOn: 'render',
                              rippleEffect: {
                                  brushType: 'stroke'
                              },
                              hoverAnimation: true,
                              rippleEffect: {
                                  scale: 5
                              },
                              label: {
                                  normal: {
                                      formatter: '{b}',
                                      show: true
                                  },
                                  emphasis: {//对应的鼠标悬浮效果
                                      show: true,
                                      textStyle:{color:"#fff"}
                                  }
                              },
                              itemStyle: {
                                  normal: {
                                      color: convertColor,
                                      shadowBlur: 10,
                                      shadowColor: '#333'
                                  }
                              },
                              zlevel: 1

                          }],
                  };

                  this.option=option;
                  this.newSeries=option.series;
                  this.newData=mapNewResult;
                  myChart.setOption(this.option);

            }

        }
    }
</script>