<template>
    <div id="echarts_3" class="charts3" ></div>

</template>
<script>
    export default {
        name:'Foldline',
        data(){
          return{
              incomeLine:[], //收入折线
              refundLine:[],      //退款折线
              option:{}
          }
        },
       async mounted(){
           let res= await this.$axios.get(this.$store.state.reqUrl+'/pc/index/line_chart');
           this.incomeLine=res.data.income;
           this.refundLine=res.data.refund;
           this.getLine();
        },
        watch:{
           option:function () {
               this.option.series[0].data=this.incomeLine;
               this.option.series[1].data=this.refundLine;
           }

        },
        methods:{
            getLine(){
                var result = [];
                var now = new Date();
                Date.prototype.getMonthDay = function(){
                    return ((this.getMonth() + 1)<10?'0'+(this.getMonth() + 1):(this.getMonth() + 1)) + '-' + ((this.getDate()-1)<10?'0'+(this.getDate()-1):(this.getDate()-1));
                }
                result.push(now.getMonthDay());

                for(var i = 0 ; i < 6 ; i ++){
                    now.setDate(now.getDate() - 1);
                    result.push(now.getMonthDay())
                }

                var results=[];
                var weekDay = ["周一","周二", "周三", "周四", "周五", "周六","周日",];
                for(var i=0;i<result.length;i++){
                    var myDate = new Date(Date.parse(result[i]));
                    results.push({value:result[i]+'('+weekDay[myDate.getDay()]+')'})
                }

                var myChart = this.$echarts.init(document.getElementById('echarts_3'));
                var option = {
                    tooltip : {
                        trigger: 'axis'
                    },
                    grid: {
                        left: '10%'

                    },

                    legend: {
                        data:['收入','退款'],
                        textStyle: {
                            color: '#fff'          // 图例文字颜色
                        }
                    },
                    xAxis : [
                        {

                            axisLabel:{
                                formatter:function (params) {
                                    var newParamsName = "";// 最终拼接成的字符串
                                    var paramsNameNumber=params.length;
                                    var paramsNameNumber2 = params.split('(')[0];// 实际标签的个数

                                    var provideNumber=4;// 每行能显示的字的个数
                                    if(paramsNameNumber2.length<=3){
                                        provideNumber=3;
                                    }else if(paramsNameNumber2.length==4){
                                        provideNumber=4;
                                    }
                                    else{
                                        provideNumber=5;
                                    }
                                    var rowNumber = Math.ceil(paramsNameNumber / provideNumber);// 换行的话，需要显示几行，向上取整
                                    /**
                                     * 判断标签的个数是否大于规定的个数， 如果大于，则进行换行处理 如果不大于，即等于或小于，就返回原标签
                                     */
                                    // 条件等同于rowNumber>1
                                    if (paramsNameNumber > provideNumber) {
                                        /** 循环每一行,p表示行 */
                                        for (var p = 0; p < rowNumber; p++) {
                                            var tempStr = "";// 表示每一次截取的字符串
                                            var start = p * provideNumber;// 开始截取的位置
                                            var end = start + provideNumber;// 结束截取的位置
                                            // 此处特殊处理最后一行的索引值
                                            if (p == rowNumber - 1) {
                                                // 最后一次不换行
                                                tempStr = params.substring(start, paramsNameNumber);
                                            } else {
                                                // 每一次拼接字符串并换行
                                                tempStr = params.substring(start, end) + "\n";
                                            }
                                            newParamsName += tempStr;// 最终拼成的字符串
                                        }

                                    } else {
                                        // 将旧标签的值赋给新标签
                                        newParamsName = params;
                                    }
                                    //将最终的字符串返回
                                    return newParamsName
                                }
                            },
                            axisLine:{
                                lineStyle :{
                                    color: '#CECECE'
                                }
                            },
                            data:results

                        }
                    ],
                    yAxis : [
                        {

                            type : 'value',
                            axisLine:{
                                lineStyle :{
                                    color: '#CECECE'
                                }
                            },
                            axisLabel: {
                                formatter: '{value}￥'
                            }
                        }
                    ],
                    series : [
                        {
                            name:'收入',
                            type:'line',
                            symbol:'none',
                            smooth: 0.2,
                            color:['#5df8ff'],
                            data:this.incomeLine
                        },
                        {
                            name:'退款',
                            type:'line',
                            symbol:'none',
                            smooth: 0.2,
                            color:['#ffec39'],
                            data:this.refundLine
                        }
                    ]

                };
                this.option=option;
                myChart.setOption(option);
            }
        }
    }
</script>