<template>
    <div class="left-bottom">
        <div class="title">行政区划收入统计TOP10</div>
        <div class="shouru_list">
            <div class="shouru_item"
                 v-for="val,key in shouru_list"
            >
                <span class="province">{{val.province}}</span>
                 <template v-if="val.status=='up'">
                     <img src="../../static/images/up_icon.png">
                 </template>
                <template v-else-if="val.status=='unchange'">
                    <em style="display: inline-block;margin: 0 36px;"></em>
                </template>
                <template v-else="val.status=='down'">
                    <img src="../../static/images/down_icon.png">
                </template>
                <span>{{val.total_income}}</span>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        name:'Shouru',
        data(){
          return{
              shouru_list:[]
          }
        },
        created(){
         this.getShouruList()
        },
        methods:{
            getShouruList(){
                this.$axios.get(this.$store.state.reqUrl+'/pc/index/top10').then(res=>{
                    this.shouru_list=res.data;
                });
            }
        }
    }
</script>