<template>
    <div class="info3" style="height: 36%;">
        <div class="ksh_left1_title">
            <p>平时/周末对比</p>
        </div>
        <div id="echarts_6" class="charts6" style="height: 100%;" ></div>
    </div>
</template>
<script>
    export default {
        name:'Weekend',
        data(){
            return{
                weekList:[]
            }
        },
        mounted() {
           this.echarts_6();
        },
        methods:{
           async echarts_6(){
                let res=await this.$axios.get(this.$store.state.reqUrl+'/pc/index/week');
                let newData=res.data;
                newData.forEach((e,i,all)=>{
                   this.weekList.push(e.value);
                })
               let newClientcounts=this.weekList;
                var myChart = this.$echarts.init(document.getElementById('echarts_6'));
                var option={
                    tooltip : {
                        trigger: 'item',
                        formatter: "{a} <br/>{b} : {c} ({d}%)"
                    },
                    color:['#14d4db','#38b8ae'],
                    legend: {
                        orient: 'vertical',
                        bottom: 50,
                        itemGap: 20,
                        left: '10%',
                        textStyle: {
                            color: '#fff'
                        },
                        formatter:function (name) {
                            var index = 0;
                            var clientlabels =['周中总收入','周末总收入'];
                            var clientcounts = newClientcounts;
                            clientlabels.forEach(function(value,i){
                                if(value == name){
                                    index = i;
                                }
                            });
                            return name + "  " + Math.floor(clientcounts[index])+'￥';
                        }
                    },
                    series:[
                        {
                            name:'总收入',
                            type:'pie',
                            center: ['50%', '30%'],
                            radius: '50%',
                            label: {
                                normal: {
                                    show: false,
                                    position: 'center'
                                },
                                emphasis: {
                                    show: false

                                }
                            },
                            data:newData
                        },
                    ]
                };
                myChart.setOption(option);
            }
        }
    }
</script>