<template>
    <div class="info2" >
        <div class="ksh_left1_title">
            <p>在线/离线设备</p>
            <h6 class="sb_num">设备总量{{totalNum}}台</h6>
        </div>
        <div id="echarts_1" class="charts" style="height: 100%;"></div>
    </div>
</template>
<script>

    export default {
        name:'LineMachine',
        data(){
            return {
                offline:'', //离线设备
                online:'', //在线设备
                totalNum:'' //设备总数
            }

        },

        mounted(){
            this.getLineMachine();
            this.echarts_1();
        },
        methods:{
           async getLineMachine() {
               let res=await this.$axios.get(this.$store.state.reqUrl+'/pc/index/online_count');
               let data=res.data.data;
               this.totalNum=data.total;
               this.online=data.online;
               this.offline=data.offline;
            },
           async echarts_1() {
               let res=await this.$axios.get(this.$store.state.reqUrl+'/pc/index/online_count');
               let data=res.data.data;
                var myChart = this.$echarts.init(document.getElementById('echarts_1'));
                var  sb_data=[
                    {name:'在线设备',value:data.online},
                    {name:'离线设备',value:data.offline},
                ];
                var option = {
                    legend: {
                        orient: 'horizontal',
                        top: '50%',
                        left:'5%',
                        formatter:function (name) {
                            var target;
                            for(var i=0,l=sb_data.length;i<l;i++){
                                if(sb_data[i].name==name){
                                    target=sb_data[i].value;
                                }
                            }
                            return name+''+target+'台';
                        },
                        itemWidth: 30,
                        itemHeight: 20,
                        textStyle: {
                            fontSize: 16,
                            color: '#ffffff',
                        }
                    },

                    grid:{
                        left:'5%',
                        top:'10%'
                    },
                    color: ['#f3e88e', '#b0f8fe'],

                    xAxis: [{
                        type: 'value',
                        show: false
                    }],
                    yAxis: [{
                        type: 'category',
                        show: false,
                        data: ['设备数量']
                    }],
                    series: [
                        {
                            name: '在线设备',

                            type: 'bar',
                            stack: '设备数量',

                            data: [
                                {
                                    value: data.online,
                                    label: {
                                        normal: {
                                            color: '#ffffff',
                                            fontSize: 16,
                                            formatter: [
                                                '{c}' + '%'
                                            ].join('\n')
                                        }
                                    }
                                }]
                        },
                        {
                            name: '离线设备',

                            type: 'bar',
                            stack: '设备数量',
                            data: [{
                                value: data.offline,
                                label: {
                                    normal: {
                                        color: '#ffffff',
                                        fontSize: 16,
                                        formatter: [
                                            '{c}' + '%'
                                        ].join('\n')
                                    }
                                }
                            }]
                        }
                    ]
                };

                myChart.setOption(option);
            }
        }
    }
</script>